.dialog {
	min-height: 63px;
	border-bottom: 1px solid rgb(220, 225, 230);

	& a {
		padding: 0 20px;
		display: grid;
		grid-template-columns: auto 4fr;
		text-decoration: none;
		color: inherit;
		gap: 7px;

		&:hover {
			background-color: rgb(0, 0, 0, 0.1);
		}
	}
}

.dialog:last-child {
	border-bottom: none;
}

.active {
	box-shadow: 0 0px 2px rgb(0, 0, 0, 0.40);
	background-color: rgb(0, 0, 0, 0.1);
}

.ava {
	width: 46px;
	height: 46px;
	margin: 9px 0 7px 0;
	border-radius: 50%;
	overflow: hidden;
}

.dialog img {
	object-fit: cover;
	width: 100%;
}

.name {
	align-self: flex-start;
	font-size: 12.5px;
	font-weight: 500;
	line-height: 14.425px;
	padding: 11px 0;
}